@import "../colors.scss";

.navbar {
  box-sizing: border-box;
  background-color: $dark;
  color: $text;

  height: 100%;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  gap: 20px;

  @media (max-width: 700px) {
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    height: 10%;
  }
}

.text-name {
  text-align: center;
  font-family: "Courier New", Courier, monospace;
  font-size: 2rem;
  font-weight: bold;

  @media (max-width: 700px) {
    font-size: 1.5rem;
  }

  @media (max-width: 400px) {
    font-size: 1.2rem;
  }
}

.navbar-link {
  font-family: "Courier New", Courier, monospace;
  font-size: 1.5rem;
  color: $text;
  text-decoration: none;

  &:hover {
    color: $text-hover;
    cursor: pointer;
  }

  @media (max-width: 700px) {
    font-size: 1rem;
  }
}
